import React from "react";
import { CopyToClipboard } from 'react-copy-to-clipboard';
import styles from "./css/ModalPlus.module.css";

function ModalPlus(props) {
  const closeModal = () => {props.setModalOpen(false); props.setWebValue(false);};
  const Color = {fontSize: 26, color: "rgba(12, 12, 12, 1)"};
  const Color_copy = {fontSize: 28, color: "rgba(12, 12, 12, 1)"};
  const Color_link = {fontSize: 31, color: "rgba(12, 12, 12, 1)"};

  return (
    <div className={styles.container}>
      <span class="material-icons modal_close" onClick={closeModal} style={Color}>close</span>
      <p class={styles.text}><span class={styles.text_span}>*  *</span>         .     PC  .    .  .</p>
      <div class={styles.text_container}>
        <div class={styles.text_copy_link_container}>
          <CopyToClipboard text="https://ongaram.github.io/synthesize_web/">
            <span class={styles.text_copy}> </span>
          </CopyToClipboard>
        </div>
        <div class={styles.text_copy_link_container}>
          <a href="https://ongaram.github.io/synthesize_web/" class={styles.text_copy}> </a>
        </div>
      </div>
    </div>
  );
}
export default ModalPlus;
