import React from "react";
import style from "../css/Copyright.module.css"

const Copyright2 = () => {
  const copyright1 = `
   : NanumPen.ttf
   : ()() 
   : (https://gongu.copyright.or.kr)
   : OFL(Open Font License)`;
  const copyright2 = `
   : GenYoMinTW-Regular.ttf
   : ZHANGDAN520
   : https://chinesefontdesign.com/yuanxiangming-traditional-free-commercial-chinese-fonts-based-on-siyuan-song-style.html
   : OFL(Open Font License)`;
  const copyright3 = `
   : GenYoMinTW-Heavy.ttf
   : ZHANGDAN520
   : https://chinesefontdesign.com/yuanxiangming-traditional-free-commercial-chinese-fonts-based-on-siyuan-song-style.html
   : OFL(Open Font License)`;

  return (
    <div className={style.container}>
      <div className={style.text}>{copyright1}</div>
      <div className={style.text}>{copyright2}</div>
      <div className={style.text}>{copyright3}</div>
    </div>
  );
};

export default Copyright2;
