import React, { useState, useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "./css/DependentIndex.module.css"
import Modal from "../function/Modal";
import ModalPlus from "../function/ModalPlus";

const Index = () => {
  const [modalOpen, setModalOpen] = useState(false);
  const [webValue, setWebValue] = useState(false);
  const showModal = () => setModalOpen(true);

  return (
    <div className={style.container_}>
      <div className={style.flex1}>
        <Link to="/DependentIndex" className={style.button_small}> </Link>
      </div>
      <div className={style.flex1}>
        <div className={style.button_small} onClick={showModal}>   </div>
      </div>
      <div className={style.flex1}>
        <Link to="/Preface" className={style.button_small}>  </Link>
      </div>
      <div className={style.flex1}>
        <Link to="/CopyrightIndex" className={style.button_small}>    </Link>
      </div>
      {modalOpen && <Modal webValue={webValue} setWebValue={setWebValue} setModalOpen={setModalOpen} />}
      {webValue && <ModalPlus webValue={webValue} setWebValue={setWebValue} setModalOpen={setModalOpen} />}
    </div>
  );
};

export default Index;
