import React, { useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "./css/DependentIndex.module.css"

const CopyrightIndex = () => {
  return (
    <div className={style.container_copyright}>
      <div className={style.flex1}>
        <Link to="/Copyright1" className={style.button_small}>1.  </Link>
      </div>
      <div className={style.flex1}>
        <Link to="/Copyright2" className={style.button_small}>2. </Link>
      </div>
      <div className={style.flex1}>
        <Link to="/Copyright3" className={style.button_small}>3. </Link>
      </div>
      <div className={style.flex1}>
        <Link to="/Copyright4" className={style.button_small}>4. </Link>
      </div>
    </div>
  );
};

export default CopyrightIndex;
